// app/page.tsx (Next.js 13+ with Tailwind CSS)
import React, { useState } from "react";
// import ReusableTextarea from "@/components/ui/TextArea";
import  Input  from "@/components/ui/Input";
// import { Button } from "@/components/ui/button";
// import { Card, CardContent } from "@/components/ui/card";

export default function Home() {
  const [url, setUrl] = useState("");
  const [result, setResult] = useState<any>(null);
  const [loading, setLoading] = useState(false);

  const handleScrape = async () => {
    setLoading(true);
    try {
      const res = await fetch("/api/scrape", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ url }),
      });
      const data = await res.json();
      setResult(data);
    } catch (error) {
      console.error("Scrape failed", error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <main className="min-h-screen bg-gray-50 py-10 px-4 sm:px-8 md:px-16">
      <div className="max-w-4xl mx-auto space-y-6">
        <h1 className="text-3xl font-bold text-gray-800">ðŸ§  AI Web Scraper</h1>
        <p className="text-gray-600">
          Paste any article or website URL. We'll scrape the content and generate enriched AI insights like summary, sentiment, SEO tags, and more.
        </p>

        <div className="space-y-4">
          <Input
            placeholder="https://example.com/article"
            value={url}
            onChange={(e) => setUrl(e.target.value)}
            className="w-full"
          />
          {/* <Button onClick={handleScrape} disabled={loading}>
            {loading ? "Scraping..." : "Start Scraping"}
          </Button> */}
        </div>

        {/* {result && (
          <Card className="mt-8">
            <CardContent className="space-y-4">
              <h2 className="text-xl font-semibold">Results</h2>
              <div>
                <strong>Headline:</strong> {result?.data?.headline || "-"}
              </div>
              <div>
                <strong>TL;DR:</strong> {result?.data?.tldr || "-"}
              </div>
              <div>
                <strong>Summary:</strong>
                <p className="text-sm text-gray-700 whitespace-pre-wrap">
                  {result?.data?.summary}
                </p>
              </div>
              <div>
                <strong>Topics:</strong> {result?.data?.topics?.join(", ") || "-"}
              </div>
              <div>
                <strong>Sentiment:</strong> {result?.data?.sentiment?.label} ({result?.data?.sentiment?.score})
              </div>
              <div>
                <strong>SEO Tags:</strong> {result?.data?.seo_tags?.join(", ") || "-"}
              </div>
            </CardContent>
          </Card>
        )} */}
      </div>
    </main>
  );
}
